import express from 'express';
import { Joi, celebrate } from 'celebrate';
import createUser from '../controllers/user_post';
import { baseUrl } from '../baseurl';

const router = express.Router();

const userSchema = {
  body: {
    userId: Joi.string().required(),
    password: Joi.string().required(),
    mobileNumber: Joi.string().required(),
    email: Joi.string().email().required(),
  },
};

/**
 * @swagger
 * /auth/user:
 *   post:
 *     tags:
 *       - Authentication
 *     description: It takes user details and creates a user.
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required user details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              userId:
 *                  type: string
 *              password:
 *                  type: string
 *              mobileNumber:
 *                  type: string
 *              email:
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              userId:
 *                  type: string
 *              mobileNumber:
 *                  type: string
 *       400:
 *         description: Returns 400 if userId or password are invalid
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.post(`${baseUrl}/user`, celebrate(userSchema), createUser);

export default router;
